// Custom checkbox using Font Awesome, for better styling, without using any JS

///

//  <input class="custom-checkbox" type="checkbox" id="checkbox_id">
//  <label class="grid grid--align-content-center" for="checkbox_id">Checkbox</label>

///

.custom-checkbox {
    display: none;
    & + label:before {
        content: "\f096";
        position: relative;
        top: 1px;
        display: inline-block;
        width: 1em;
        margin-right: 0.2em;
        font-family: FontAwesome; }
    &:checked + label:before {
        content: "\f046"; } }
